export default {
	data() {
		return {
			addressList: [],
			select:false,
			userlist:''
		};
	},
	onLoad(options) {
		this.userlist = uni.getStorageSync('userlist'); //加载用户缓存
		console.log(this.userlist);
		if(options.set || options.select){
			this.select = true;
		}
	},
	onShow() {
		this.getAddressList();
	},
	methods: {
		// 选择地址
		setAddr(item){
			if(this.select){
				uni.setStorageSync('setAddr',item)
				this.utils.navback();
			}
		},
		// 获取地址列表
		getAddressList() {
			this.utils.showloading();
			this.http.getApi('/shippings/Getlist', {uid:this.userlist.id, pageNum: 1,pageSize:20 }, 'get',this.userlist.storeOpendid).then(res => {
					uni.hideLoading();
					this.addressList = res.data.list;
					console.log(res);
				})
				.catch(err => {
					uni.hideLoading();
					console.log(err);
				});
		},
		edit(item){
			uni.setStorageSync('edit_addr',item);
			this.doUrl('pages/address/add_address', {edit:true})
		}
	}
};
